* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative; }

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121;
  z-index: 1; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0px;
  list-style-type: none;

  .menu_link {
    color: #ffffff;
    font-weight: 300;
    font-size: 15px; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  margin-top: 50px;
  position: absolute;
  width: 100%;
  height: 87px;
  background: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    margin-top: 16px;
    max-width: 100%; }

  .subheader_official {
    margin-top: 6px;
    opacity: 0.5;
    color: #FFFFFF;
    font-weight: 400;
    font-size: 13px; }

  .subheader_call {
    text-align: right;
    margin-top: 20px;
    color: #FFFFFF;
    font-weight: 300;
    font-size: 12px; }

  .subheader_phone {
    display: block;
    margin-top: 8px;
    text-align: right;
    color: #FFFFFF;
    font-weight: 400;
    font-size: 22px;
    text-transform: uppercase;
    line-height: 12px; }

  .subheader_btn {
    margin-top: 20px;
    width: 172px;
    height: 46px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase; } }

.promo {
  min-height: 800px;
  background: url('../img/uber_bg.png') center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    color: #ffffff;
    font-weight: 700;
    font-size: 60px;
    text-transform: uppercase;
    line-height: 60px;
    margin: 0px;
    text-align: center; }

  .promo_subheader {
    color: #ffffff;
    font-weight: 700;
    font-size: 36px;
    text-transform: uppercase;
    line-height: 36px;
    margin: 0px;
    margin-top: 22px;
    text-align: center; }

  .promo_descr {
    color: #ffffff;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    font-weight: normal;
    font-size: 18px;
    color: #FFFFFF;
    align-items: center;
    font-weight: 400;
    text-transform: uppercase; } }

.reasons {
  padding: 71px 0 65px 0; }

.label {
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center; }

.title {
  margin-bottom: 0;
  margin-top: 38px;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center; }

.subtitle {
    margin-top: 19px;
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    text-align: center; }

.reasons .reasons_block {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;

  .reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%; }

  .reasons_descr {
    width: 343px;

    .reasons_subtitle {
      line-height: 21px;
      color: #1eacc7;
      font-size: 17px;
      font-weight: 400; }

    .reasons_text {
      color: #202020;
      font-size: 15px;
      font-weight: 300;
      line-height: 21px; } } }

.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../img/mobile_bg.png') center (center / cover) no-repeat;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 200px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background: url('../img/mobile_screen_1.jpg') center (center / cover) no-repeat; }
        &_2 {
            background: url('../img/mobile_screen_2.jpg') center (center / cover) no-repeat; }
        &_3 {
            background: url('../img/mobile_screen_3.jpg') center (center / cover) no-repeat; }
        &_4 {
            background: url('../img/mobile_screen_4.jpg') center (center / cover) no-repeat; }
        &_5 {
            background: url('../img/mobile_screen_5.jpg') center (center / cover) no-repeat; }
        &_6 {
            background: url('../img/mobile_screen_6.jpg') center (center / cover) no-repeat; }
        &_subtitle {
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            line-height: 31px;
            text-transform: uppercase;
            text-align: center; }

        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            width: 26px;
            height: 26px;
            background-color: #000000;
            margin: 0 auto;
            border-radius: 100%;
            padding: 6px; } } }




